import React from 'react';
import { Button } from '../Button';
import { cn } from '../../utils';

/**
 * BackButton Component
 * Reusable back button for navigation
 */
export const BackButton = ({
  onClick,
  label = '← Quay lại',
  className = '',
  inline = false,
  variant = 'outline',
  size,
  ...props
}) => {
  const containerClasses = inline ? 'mt-0 text-left' : 'mt-8 text-center';
  const buttonSize = size || (inline ? 'sm' : 'md');

  return (
    <div className={cn(containerClasses, className)}>
      <Button
        onClick={onClick}
        variant={variant}
        size={buttonSize}
        {...props}
      >
        {label}
      </Button>
    </div>
  );
};

